.DataAnalyticsProjects {
    width: 100vw;
    height: 100vh;
    overflow: scroll;
    overflow-x: hidden;
    scrollbar-width: thin;
    scrollbar-color: white deepskyblue;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.DataAnalyticsProjects > h1 {
    color: white;
    font-size: 2.25rem;
    font-weight: 500;
    text-align: center;
    margin-top: 83.5rem;
}

.grid-container--analytics {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 8rem;
    max-width: 960px;
    margin-top: 4rem;
}

.exploration-sql {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.exploration-sql > h2 {
    text-align: center;
    font-size: 1.5rem;
    font-weight: 500;
    color: white;
}

.exploration-sql > p {
    max-width: 26rem;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.image--exploration-sql {
    max-width: 26rem;
    border-radius: 0.5rem;
    margin: 1.5rem 0;
}

