    font-size: 1.1rem;
    color: white;
    margin-right: 0.25rem;
}

.github-data > a {
    font-size: 2rem;
    color: white;
    margin-left: 0.25rem;
}

.github-data > a:hover {
    color: gold;
}

@media screen and (max-width: 720px) {
    .DataAnalyticsProjects > h1 {
        margin-top: 164.5rem;
    }

    .grid-container--analytics {
        display: grid;
        grid-template-columns: 1fr;
        gap: 6rem;
    }

    .more-projects-data {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: 6rem 0 8rem 0;
    }
}

@media screen and (max-width: 420px) {
    .DataAnalyticsProjects > h1 {
        margin-top: 138.5rem;
        max-width: 30rem;
    }

    .grid-container--analytics {
        display: grid;
        grid-template-columns: 1fr;
        gap: 4rem;
    }

    .image--exploration-sql {
        max-width: 20rem;
    }

    .exploration-sql > p {
        max-width: 20rem;
    }

    .image--data-cleaning {
        max-width: 20rem;
    }

    .data-cleaning > p {
