.SoftwareDevelopmentProjects {
    width: 100vw;
    height: 100vh;
    overflow: scroll;
    overflow-x: hidden;
    scrollbar-width: thin;
    scrollbar-color: white deepskyblue;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.SoftwareDevelopmentProjects > h1 {
    color: white;
    font-size: 2.25rem;
    font-weight: 500;
    text-align: center;
    margin-top: 58rem;
}

.grid-container--development {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 8rem;
    max-width: 960px;
    margin-top: 4rem;
}

.quizzical {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.quizzical > h2 {
    text-align: center;
    font-size: 1.5rem;
    font-weight: 500;
    color: white;
}

.quizzical > p {
    max-width: 26rem;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.image--quizzical {
    max-width: 13rem;
    border-radius: 0.5rem;
    margin: 1.5rem 0;
}

.drum-machine {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.drum-machine > h2 {
    text-align: center;
    font-size: 1.5rem;
    font-weight: 500;
    color: white;
}

.drum-machine > p {
    max-width: 26rem;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.image--drum-machine {
    max-width: 26rem;
    border-radius: 0.5rem;
    margin: 1.5rem 0;
}

.react-calculator {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.react-calculator > h2 {
    text-align: center;
    font-size: 1.5rem;
    font-weight: 500;
    color: white;
}

.react-calculator > p {
    max-width: 26rem;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.image--react-calculator {
    max-width: 13rem;
    border-radius: 0.5rem;
    margin: 1.5rem 0;
}

.pomodoro-timer {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.pomodoro-timer > h2 {
    text-align: center;
    font-size: 1.5rem;
    font-weight: 500;
    color: white;
}

.pomodoro-timer > p {
    max-width: 26rem;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.image--pomodoro-timer {
    max-width: 26rem;
    border-radius: 0.5rem;
    margin: 1.5rem 0;
}

.more-projects-software {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 12rem 0 8rem 0;
}

.projects-software {
    font-size: 1.1rem;
    color: white;
    margin-right: 0.25rem;
}

.github-software > a {
    font-size: 2rem;
    color: white;
    margin-left: 0.25rem;
}

.github-software > a:hover {
    color: gold;
}

@media screen and (max-width: 720px) {
    .SoftwareDevelopmentProjects > h1 {
        margin-top: 112.5rem;
    }

    .grid-container--development {
        display: grid;
        grid-template-columns: 1fr;
        gap: 6rem;
    }

    .more-projects-software {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: 6rem 0 8rem 0;
    }
}

@media screen and (max-width: 420px) {
    .SoftwareDevelopmentProjects > h1 {
        margin-top: 102rem;
        max-width: 30rem;
    }

    .grid-container--development {
        display: grid;
        grid-template-columns: 1fr;
        gap: 4rem;
    }

    .quizzical {
        max-width: 20rem;
    }

    .image--quizzical {
        max-width: 10rem;
    }

    .image--drum-machine {
        max-width: 20rem;
    }

    .drum-machine > p {
        max-width: 20rem;
    }

    .image--react-calculator {
        max-width: 10rem;
    }

    .react-calculator > p {
        max-width: 20rem;
    }

    .image--pomodoro-timer {
        max-width: 20rem;
    }

    .pomodoro-timer > p {
        max-width: 20rem;
    }
}
